import React from "react";
import { Card, CardContent, CardHeader, CardTitle } from "../ui/card";
import { Button } from "../ui/button";

export default function PeriodSelector({ days, setDays }) {
  return (
    <Card className="shadow-sm">
      <CardHeader>
        <CardTitle className="text-base"> </CardTitle>
      </CardHeader>
      <CardContent className="space-y-3">
        <div className="flex items-center gap-2">
          <Button variant={days === 30 ? "default" : "secondary"} onClick={() => setDays(30)}>30</Button>
          <Button variant={days === 60 ? "default" : "secondary"} onClick={() => setDays(60)}>60</Button>
          <Button variant={days === 90 ? "default" : "secondary"} onClick={() => setDays(90)}>90</Button>
          <Button variant={days === 180 ? "default" : "secondary"} onClick={() => setDays(180)}>6</Button>
        </div>
        <div className="text-xs text-slate-500">
            7/10~8/8 .     CSV .
        </div>
      </CardContent>
    </Card>
  );
}