//  CSV  - BOM   

export function robustParseCSV(text) {
  if (!text) return [];
  
  // 1.   BOM 
  let cleanText = text;
  
  // UTF-8 BOM
  if (cleanText.charCodeAt(0) === 0xFEFF) {
    cleanText = cleanText.substring(1);
  }
  
  //   BOM
  cleanText = cleanText.replace(/^\uFEFF/, '');
  cleanText = cleanText.replace(/^\xEF\xBB\xBF/, '');
  cleanText = cleanText.replace(/^/, '');
  
  //   
  cleanText = cleanText.trim();
  
  // 2.   
  const lines = cleanText.split(/\r?\n/).filter(line => line.trim().length > 0);
  
  if (lines.length < 2) {
    console.warn('CSV has less than 2 lines');
    return [];
  }
  
  // 3.   (  )
  const firstLine = lines[0];
  let separator = ',';
  
  if (firstLine.includes('\t')) {
    separator = '\t';
  }
  
  // 4.  
  const headers = parseLine(firstLine, separator).map(h => {
    //  BOM 
    let clean = h.trim();
    clean = clean.replace(/^\uFEFF/, '');
    clean = clean.replace(/^\xEF\xBB\xBF/, '');
    clean = clean.replace(/^/, '');
    return clean;
  });
  
  console.log('Parsed headers:', headers);
  
  // 5.   
  const data = [];
  for (let i = 1; i < lines.length; i++) {
    const values = parseLine(lines[i], separator);
    const row = {};
    
    headers.forEach((header, index) => {
      row[header] = values[index] || '';
    });
    
    data.push(row);
  }
  
  return data;
}

// CSV   (  )
function parseLine(line, separator) {
  const result = [];
  let current = '';
  let inQuotes = false;
  
  for (let i = 0; i < line.length; i++) {
    const char = line[i];
    const nextChar = line[i + 1];
    
    if (char === '"') {
      if (inQuotes && nextChar === '"') {
        //    
        current += '"';
        i++; //   
      } else {
        //   
        inQuotes = !inQuotes;
      }
    } else if (char === separator && !inQuotes) {
      //       
      result.push(current.trim());
      current = '';
    } else {
      current += char;
    }
  }
  
  //   
  result.push(current.trim());
  
  return result;
}

//     
export function parseInvestorFlowData(text) {
  const rows = robustParseCSV(text);
  
  if (rows.length === 0) {
    console.warn('No rows parsed from investor flow data');
    return [];
  }
  
  //  
  const firstRow = rows[0];
  const headers = Object.keys(firstRow);
  console.log('Investor flow headers:', headers);
  
  //   
  const dateField = headers.find(h => 
    h === '' || 
    h === 'date' || 
    h === 'Date' || 
    h.includes('')
  );
  
  if (!dateField) {
    console.warn('No date field found, using first column');
  }
  
  //  
  return rows.map((row, idx) => {
    const normalized = {};
    
    // 
    normalized.date = row[dateField] || row[headers[0]] || '';
    
    //   (   )
    const investorKeys = [
      '', '', '', '', '', 
      '', '', '', '', 
      '', '', ''
    ];
    
    investorKeys.forEach(key => {
      const value = row[key];
      normalized[key] = parseNumber(value);
    });
    
    if (idx === 0) {
      console.log('First normalized row:', normalized);
    }
    
    //   
    if (!normalized[''] || normalized[''] === 0) {
      const institutionKeys = [
        '', '', '', '', '', 
        '', '', ''
      ];
      normalized[''] = institutionKeys.reduce((sum, key) => 
        sum + (normalized[key] || 0), 0
      );
    }
    
    //  
    normalized[''] = (normalized[''] || 0) + (normalized[''] || 0);
    
    return normalized;
  }).filter(row => row.date); //    
}

//   
function parseNumber(value) {
  if (typeof value === 'number') return value;
  if (!value) return 0;
  
  const str = String(value).trim();
  if (str === '' || str === '-') return 0;
  
  //  
  const cleaned = str.replace(/,/g, '');
  
  //  
  const num = Number(cleaned);
  return isFinite(num) ? num : 0;
}

export default {
  robustParseCSV,
  parseInvestorFlowData
};